<template>
  <div v-if="modelValue">
    <!--  -->
    <LineOption :custom="true">
      <template #default>
        <div class="sub-title">
          <div class="sub-title"></div>
          <div class="sub-title-info">
            
          </div>
        </div>
        <div class="sub-title">
          <div class="sub-title"></div>
          <div class="sub-title-info">
             Bukkit
            
            Bukkit 
          </div>
        </div>
      </template>
    </LineOption>

    <!--  Map  -->
    <div v-for="(item, index) in config" :key="index">
      <!-- , -->
      <LineOption :option-value="config" :option-key="index">
        <!--  -->
        <template #title>{{ index }}</template>
        <!--  -->
        <template #info>{{ descriptionByTitle(description, index) }}</template>
      </LineOption>
    </div>
  </div>
</template>

<script>
import { getDescriptionByTitle, jsonToMap } from "../../app/service/common";
import LineOption from "../LineOption";
import LineOptionList from "../LineOptionList";

export default {
  // eslint-disable-next-line vue/no-unused-components
  components: { LineOption, LineOptionList },
  props: {
    modelValue: Object
  },
  async mounted() {
    // JSON Map 
    this.config = jsonToMap(this.modelValue);
  },
  methods: {
    descriptionByTitle(...p) {
      //  Map  Key  JSON  Value
      return getDescriptionByTitle(...p);
    }
  },
  data() {
    return {
      config: null,
      description: {
        settings: {
          "allow-end": "",
          "warn-on-overload": "",
          "permissions-file": "",
          "update-folder":
            "",
          "plugin-profiling": "/timings",
          "connection-throttle": "",
          "query-plugins": "MCSM",
          "deprecated-verbose": "",
          "shutdown-message": "",
          "minimum-api": ""
        },
        "spawn-limits": {
          monsters: "",
          animals: "",
          "water-animals": "",
          "water-ambient": "",
          ambient: ""
        },
        "chunk-gc": {
          "period-in-ticks": "Tick"
        },
        "ticks-per": {
          "animal-spawns":
            "Tick400400Tick0Minecraft",
          "monster-spawns": "Tick",
          autosave:
            "60005:",
          "water-spawns": "",
          "water-ambient-spawns": "",
          "ambient-spawns": ""
        },
        aliases: ""
      }
    };
  }
};
</script>
