<template>
  <!--  -->
  <LineOption :custom="true">
    <template #default>
      <div class="sub-title">
        <div class="sub-title"></div>
        <div class="sub-title-info">
          
        </div>
      </div>
      <div class="sub-title">
        <div class="sub-title"></div>
        <div class="sub-title-info">
           BungeeCord
          
        </div>
      </div>
    </template>
  </LineOption>

  <div v-if="modelValue">
    <!--  Map  -->
    <div v-for="(item, index) in config" :key="index">
      <!-- , -->
      <LineOption :option-value="config" :option-key="index">
        <!--  -->
        <template #title>{{ index }}</template>
        <!--  -->
        <template #info>{{ descriptionByTitle(description, index) }}</template>
      </LineOption>
    </div>
  </div>
</template>

<script>
import { getDescriptionByTitle, jsonToMap } from "../../app/service/common";
import LineOption from "../LineOption";
import LineOptionList from "../LineOptionList";

export default {
  // eslint-disable-next-line vue/no-unused-components
  components: { LineOption, LineOptionList },
  props: {
    modelValue: Object
  },
  async mounted() {
    // JSON Map 
    this.config = jsonToMap(this.modelValue);
  },
  methods: {
    descriptionByTitle(...p) {
      //  Map  Key  JSON  Value
      return getDescriptionByTitle(...p);
    }
  },
  data() {
    return {
      config: null,
      description: {
        prevent_proxy_connections: " Mojang  IP ",
        listeners: [
          {
            query_port: "UDP",
            motd: "Motdping_passthrough",
            tab_list: " TAB ",
            query_enabled: " UDP ",
            proxy_protocol: " HAProxy ",
            forced_hosts: "",
            ping_passthrough: " ping ",
            priorities: "",
            bind_local_address: " BungeeCord  IP ",
            host: " IP ",
            max_players:
              "1",
            tab_size: " TAB ",
            force_default_server: ""
          }
        ],
        remote_ping_cache: "",
        network_compression_threshold: "",
        permissions: {
          default: "",
          admin: ""
        },
        log_pings: " BungeeCord  ping ",
        connection_throttle: "",
        connection_throttle_limit: "",
        server_connect_timeout: "",
        timeout: "",
        player_limit: " BungeeCord -1",
        ip_forward: " IP ",
        groups: "",
        remote_ping_timeout: "",
        log_commands: " BungeeCord ",
        stats: "",
        online_mode: "",
        forge_support: " Forge ",
        disabled_commands: "",
        servers: ""
      }
    };
  }
};
</script>
