<template>
  <LineOption :custom="true">
    <template #default>
      <div class="sub-title">
        <div class="sub-title"></div>
        <div class="sub-title-info">
          
        </div>
      </div>
      <div class="sub-title">
        <div class="sub-title"></div>
        <div class="sub-title-info">
           Mohist 
        </div>
      </div>
    </template>
  </LineOption>

  <div v-if="modelValue">
    <div v-for="(item, index) in config" :key="index">
      <LineOption :option-value="config" :option-key="index">
        <template #title>{{ index }}</template>
        <template #info>{{ descriptionByTitle(description, index) }}</template>
      </LineOption>
    </div>
  </div>
</template>

<script>
import { getDescriptionByTitle, jsonToMap } from "../../app/service/common";
import LineOption from "../LineOption";

export default {
  components: { LineOption },
  props: {
    modelValue: Object
  },
  async mounted() {
    // JSON Map 
    this.config = jsonToMap(this.modelValue);
  },
  methods: {
    descriptionByTitle(...p) {
      //  Map  Key  JSON  Value
      return getDescriptionByTitle(...p);
    }
  },
  data() {
    return {
      config: null,
      description: {
        mohist: {
          check_update: "",
          lang: "",
          check_libraries: "",
          disable_mods_blacklist: " MOD ",
          disable_plugins_blacklist: "",
          check_update_auto_download: "",
          use_custom_java8: "",
          showlogo: " Logo",
          optimize_explosions: "",
          prevent_from_entering_unloaded_chunks: "",
          ignore_empty_time: ""
        },
        libraries_black_list: ""
      }
    };
  }
};
</script>
