<template>
  <LineOption :custom="true">
    <template #default>
      <div class="sub-title">
        <div class="sub-title"></div>
        <div class="sub-title-info">
          
        </div>
      </div>
      <div class="sub-title">
        <div class="sub-title"></div>
        <div class="sub-title-info">
           Geyser 
        </div>
      </div>
    </template>
  </LineOption>

  <div v-if="modelValue">
    <div v-for="(item, index) in config" :key="index">
      <LineOption :option-value="config" :option-key="index">
        <template #title>{{ index }}</template>
        <template #info>{{ descriptionByTitle(description, index) }}</template>
      </LineOption>
    </div>
  </div>
</template>

<script>
import { getDescriptionByTitle, jsonToMap } from "../../app/service/common";
import LineOption from "../LineOption";

export default {
  components: { LineOption },
  props: {
    modelValue: Object
  },
  async mounted() {
    // JSON Map 
    this.config = jsonToMap(this.modelValue);
  },
  methods: {
    descriptionByTitle(...p) {
      //  Map  Key  JSON  Value
      return getDescriptionByTitle(...p);
    }
  },
  data() {
    return {
      config: null,
      description: {
        bedrock: {
          address: "GeyserIP",
          port: "",
          "clone-remote-port": "GeyserJava",
          motd1: "MOTDpassthrough-motdtrue",
          motd2: "MOTD",
          "server-name": ""
        },
        remote: {
          address: "JavaIP",
          port: "Java",
          "auth-type": "onlineofflinefloodgate"
        },
        "floodgate-key-file": "Floodgate  Floodgate ",
        "command-suggestions": "",
        "passthrough-motd": "JavaMOTD",
        "passthrough-protocol-name": "Java",
        "passthrough-player-counts": "Java",
        "legacy-ping-passthrough":
          "LEGACY ping passthrough MOTD ",
        "ping-passthrough-interval": "ping Java",
        "max-players": "",
        "debug-mode": "",
        "general-thread-pool": "",
        "allow-third-party-capes":
          "OptiFineLabyMod5ZigMinecraft",
        "allow-third-party-ears": " deadmau5 ears",
        "show-cooldown": "",
        "default-locale": "",
        "cache-chunks": "",
        "cache-images":
          " 0 0",
        "above-bedrock-nether-building": " Y=127 ",
        metrics: {
          enabled: "bStats",
          uuid: "UUID"
        },
        "scoreboard-packet-threshold":
          "",
        "enable-proxy-connections": " ProxyPass  Waterdog ",
        "config-version": ""
      }
    };
  }
};
</script>
