<template>
  <el-row :gutter="20">
    <el-col :span="24">
      <Panel>
        <template #title></template>
        <template #default>
          <el-row :gutter="20" class="row-mb">
            <el-col :md="12" :offset="0" class="col-md-responsive">
              <el-input
                v-model="query.userName"
                type="text"
                placeholder=""
                size="small"
                style="width: 180px; margin-right: 10px"
                autocomplete="off"
                :readonly="readonly"
                @focus="() => (readonly = false)"
              ></el-input>
              <el-button size="small" type="primary" @click="refresh">
                <i class="el-icon-refresh"></i> /
              </el-button>
            </el-col>
            <el-col :md="12" :offset="0" class="text-align-right col-md-responsive">
              <el-button size="small" type="success" @click="toNewUserPanel">
                <i class="el-icon-plus"></i> 
              </el-button>
              <el-button size="small" type="danger" @click="deleteUser">
                <i class="el-icon-delete"></i> 
              </el-button>
            </el-col>
          </el-row>

          <div class="instance-table-warpper">
            <div></div>
            <div>
              <el-pagination
                background
                layout="prev, pager, next"
                :total="maxPage"
                v-model:currentPage="page"
                :page-size="1"
                @current-change="handleCurrentChange"
                small
              ></el-pagination>
            </div>
          </div>

          <el-table
            :data="objects"
            stripe
            style="width: 100%"
            size="small"
            ref="multipleTable"
            @selection-change="selectionChange"
          >
            <el-table-column type="selection" width="55"> </el-table-column>
            <el-table-column prop="uuid" label="UUID" width="240"></el-table-column>
            <el-table-column prop="userName" label=""></el-table-column>
            <el-table-column prop="permission" label=""></el-table-column>
            <el-table-column prop="registerTime" label=""></el-table-column>
            <el-table-column prop="loginTime" label=""></el-table-column>
            <el-table-column label="" style="text-align: center" width="180">
              <template #default="scope">
                <el-button size="mini" @click="toEditUserPanel(scope.row)"></el-button>
                <el-button size="mini" @click="toAssignPanel(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </Panel>
    </el-col>
  </el-row>

  <!--  -->
  <Dialog v-model="isNewUser">
    <template #title></template>
    <template #default>
      <div>
        <div class="sub-title">
          <p class="sub-title-title"></p>
          <p class="sub-title-info">612</p>
        </div>
        <el-input
          v-model="newUserInfo.userName"
          placeholder="..."
          size="small"
        ></el-input>
        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
          <p class="sub-title-info">618</p>
        </div>
        <el-input
          v-model="newUserInfo.password"
          placeholder="..."
          size="small"
          type="password"
        ></el-input>
        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
          <p class="sub-title-info"></p>
        </div>
        <el-select v-model="newUserInfo.permission" placeholder="" size="small">
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="10"></el-option>
          <el-option label="" :value="-1"></el-option>
        </el-select>
        <div class="row-mt">
          <el-button type="success" size="small" @click="createUser"></el-button>
          <el-button @click="isNewUser = !isNewUser" size="small"></el-button>
        </div>
      </div>
    </template>
  </Dialog>

  <!--  -->
  <Dialog v-model="isEditUser">
    <template #title></template>
    <template #default>
      <div>
        <div class="sub-title">
          <p class="sub-title-title"></p>
          <p class="sub-title-info">612</p>
        </div>
        <el-input
          v-model="editUserInfo.userName"
          placeholder="..."
          size="small"
        ></el-input>
        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
          <p class="sub-title-info"></p>
        </div>
        <el-input v-model="editUserInfo.passWord" placeholder="" size="small"></el-input>
        <div class="sub-title row-mt">
          <p class="sub-title-title"></p>
          <p class="sub-title-info"></p>
        </div>
        <el-select v-model="editUserInfo.permission" placeholder="" size="small">
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="10"></el-option>
          <el-option label="" :value="-1"></el-option>
        </el-select>
        <div class="row-mt">
          <el-button type="success" size="small" @click="updateUser"></el-button>
          <el-button @click="isEditUser = !isEditUser" size="small"></el-button>
        </div>
      </div>
    </template>
  </Dialog>

  <!--  -->
