  </Dialog> -->
</template>

<script>
import Panel from "../../components/Panel";
import Dialog from "../../components/Dialog";
import { request } from "../service/protocol";
import { API_SERVICE, API_USER, API_SEARCH } from "../service/common";
// import qs from "qs";

export default {
  components: { Panel, Dialog },
  data() {
    return {
      page: 1,
      maxPage: 1,
      query: {
        userName: ""
      },
      newUserInfo: {
        userName: "",
        password: "",
        permission: 1
      },
      isEditUser: false,
      isNewUser: false,
      // isAssign: false,
      // isAssignLoading: true,
      editUserInfo: {},
      objects: [],
      remoteObjects: [], // 
      instances: [], // 
      multipleSelection: [], // 

      readonly: true
    };
  },
  methods: {
    // 
    async render() {
      const result = await request({
        method: "GET",
        url: API_SEARCH,
        params: {
          userName: this.query.userName,
          page: this.page,
          page_size: 50
        }
      });
      this.maxPage = result.maxPage;
      this.page = result.page;
      this.objects = result.data;
      this.query.userName = "";
    },
    async renderServices() {
      this.remoteObjects = await request({
        method: "GET",
        url: API_SERVICE
      });
      this.remoteObjects.forEach((v) => {
        v.instances.forEach((instance) => {
          this.instances.push({
            instanceUuid: instance.instanceUuid,
            serviceUuid: v.uuid,
            nickname: instance.config.nickname
          });
        });
      });
    },
    async requestUserInfo(uuid) {
      return await request({
        method: "GET",
        url: API_USER,
        params: {
          uuid: uuid,
          advanced: true
        }
      });
    },
    // 
    handleCurrentChange() {
      this.refresh();
    },
    // 
    selectionChange(v) {
      this.multipleSelection = v;
    },
    toEditUserPanel(row) {
      this.editUserInfo = row;
      this.isEditUser = true;
    },
    toNewUserPanel() {
      this.isNewUser = true;
    },
    async toAssignPanel(row) {
      const uuid = row.uuid;
      this.$router.push({ path: `/user_resources/${uuid}` });
      // this.isAssign = true;
      // this.isAssignLoading = true;
      // this.editUserInfo = {};
      // // 
      // this.editUserInfo = await this.requestUserInfo(row.uuid);
      // this.isAssignLoading = false;
    },
    assignInstance(row) {
      this.editUserInfo.instances.push({
        uuid: row.instanceUuid,
        region: row.serviceUuid,
        instanceInfo: {
          config: {
            nickname: row.nickname
          }
        }
      });
    },
    cancelInstace(uuid) {
      const targetUuid = uuid;
      this.editUserInfo.instances.forEach((v, index) => {
        if (v.uuid == targetUuid) this.editUserInfo.instances.splice(index, 1);
      });
    },
    async updateUser() {
      try {
        await request({
          method: "PUT",
          url: API_USER,
          data: {
            uuid: this.editUserInfo.uuid,
            config: this.editUserInfo
          }
        });
        this.$message({ type: "success", message: "" });
        this.isEditUser = false;
        this.isAssign = false;
      } catch (error) {
        this.$message({
          type: "error",
          message: `: ${error.message}`
        });
      }
      this.refresh();
    },
    async createUser() {
      try {
        await request({
          method: "POST",
          url: API_USER,
          data: {
            username: this.newUserInfo.userName,
            password: this.newUserInfo.password,
            permission: this.newUserInfo.permission
          }
        });
        this.$message({ type: "success", message: "" });
        this.isNewUser = !this.isNewUser;
      } catch (error) {
        this.$message({
          type: "error",
          message: `: ${error.message}`
        });
      }
      this.refresh();
    },
    async deleteUser() {
      try {
        const uuids = [];
        for (const v of this.multipleSelection) {
          uuids.push(v.uuid);
        }
        await request({
          method: "DELETE",
          url: API_USER,
          data: uuids
        });
        this.$message({ type: "success", message: "" });
      } catch (error) {
        this.$message({
          type: "error",
          message: `: ${error.message}`
        });
      }
      this.refresh();
    },
    refresh() {
      this.render();
    }
  },
  async mounted() {
    // 
    await this.render();
    // 
    this.renderServices();
  }
};
</script>
